import React, { useState } from 'react'
function SuggestionComponent(props) {
  const [check, setCheck] = useState(false);
  const [body,setBody]=useState('');
  const user_id=props.user_id;
  const post_id=props.post_id;
  const handlesubmit=async()=>{
    const response = await fetch(`https://mern.up.railway.app/submitsuggestion/${user_id}/${post_id}`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          body: body,
          user_id: user_id,
        }),
      });
      if(response.ok){

        alert("Suggestion is Sent");
      }
      else{
        alert("Inavlid Server response!!!");
      }
      setCheck(false);
      setBody('');
  }
  return (
    <div>
      {(!check) ?
        <div className='d-flex justify-content-end'><button className='btn btn-secondary' onClick={() => { setCheck(true); }}>
          Suggest?
        </button>
        </div>
        :
        <div className='container' >
          <div className='d-flex justify-content-end'><button className='btn btn-info' onClick={() => { setCheck(false); }}>
          Close
        </button>
        </div>
          <label>Enter Suggestion</label>
          <textarea className="form-control"  rows="3" value={body} onChange={(e)=>setBody(e.target.value)} placeholder='Enter Suggestion'></textarea>
          <button className='btn btn-success' onClick={handlesubmit}>Submit</button>
        </div>
      }
    </div>
  )
}

export default SuggestionComponent