import React, { useEffect, useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';

function UpdateUser() {
  const [userData, setUserData] = useState({});
  const { id } = useParams();
  const nav=useNavigate();
  useEffect(() => {
    // Fetch all users
    const fetchUsers = async () => {
      try {
        const usersResponse = await fetch(`https://mern.up.railway.app/users/${id}`);
        const usersData = await usersResponse.json();
        if (usersData) {
          setUserData(usersData);
        } else {
          console.error(`User with ID ${id} not found`);
          
        }
      } catch (error) {
        console.error('Error fetching users data:', error);
      }
    };

    fetchUsers();
  }, [id]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setUserData((prevData) => ({ ...prevData, [name]: value }));
  };

  const handelDelete=async ()=>{
    try {
        // Make API call to update user data
        const response = await fetch(`https://mern.up.railway.app/deleteuser/${id}`, {
          method: 'DELETE',
          headers: {
            'Content-Type': 'application/json',
          }
        });
       (response)?nav('/Admin/users'):alert('Invalid Server Response');
  
      } catch (error) {
        console.error('Error Deleting user data:', error);
      }
  }
  const handleUpdate = async () => {
    try {
      // Make API call to update user data
      const response = await fetch(`https://mern.up.railway.app/updateuser/${id}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(userData),
      });
     (response)?nav('/Admin/users'):alert('Invalid Server Response');

    } catch (error) {
      console.error('Error updating user data:', error);
    }
  };
  return (
    <div className='container my-5'>
      <h2>Update User</h2>
      <div className='my-3'>
        <label>Name:</label>
        <input type="text" name="name" value={userData.name || ''}  onChange={handleChange} />
      </div>
      <div className='my-3'>
        <label>Email:</label>
        <input type="text" name="email" value={userData.email || ''}  onChange={handleChange} />
      </div>
      <div className='my-3'>
        <label>Password:</label>
        <input type="text" name="pass" value={userData.pass || ''} onChange={handleChange}/>
      </div>
      <div className='my-3'>
        <label>ID:</label>
        <span>{userData.user_id}</span>
      </div>
      <div className='my-3'>
        <label>Status:</label>
        <span>{userData.status}</span>
      </div>
      <button className='btn btn-primary' onClick={handleUpdate} style={{marginRight:30}}>Update</button> 
      <button className='btn btn-danger' onClick={handelDelete}>Delete</button>
      <link href="https://cdn.jsdelivr.net/npm/bootstrap@5.3.2/dist/css/bootstrap.min.css" rel="stylesheet" integrity="sha384-T3c6CoIi6uLrA9TneNEoa7RxnatzjcDSCmG1MXxSR1GAsXEV/Dwwykc2MPK8M2HN" crossOrigin="anonymous"></link>
    
    </div>
  );
}

export default UpdateUser;
