import React, { useState } from 'react';
import ReactQuill from 'react-quill';
import DOMPurify from 'dompurify'; // Import DOMPurify
import 'react-quill/dist/quill.snow.css';
import { Link, useParams } from 'react-router-dom';

function CreatePost() {
  const [title, setTitle] = useState('');
  const [body, setBody] = useState('');
  const { id } = useParams();
  const userId = parseInt(id);

  const clicked = async () => {
    const sanitizedBody = DOMPurify.sanitize(body);
    const rawData = new Date();
    const date = rawData.getDate() + '/' + (rawData.getMonth() + 1) + '/' + rawData.getFullYear();
    const time = rawData.getHours() + ':' + rawData.getMinutes();

    // Sanitize the HTML content
    

    const response = await fetch('https://mern.up.railway.app/createpost', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        user_id: userId,
        title: title,
        body: sanitizedBody, // Use sanitized body
        date: date,
        time: time,
        status: 'pending',
      }),
    });

    console.log(response);
  };

  const modules = {
    toolbar: [
      [{ 'header': '1' }, { 'header': '2' }],
      ['bold', 'italic', 'underline', 'strike', 'blockquote'],
      [{ 'list': 'ordered' }, { 'list': 'bullet' }],
      ['link', 'image', 'video'],
      ['clean'],
    ],
  };

  const formats = [
    'header', 'font', 'size',
    'bold', 'italic', 'underline', 'strike', 'blockquote',
    'list', 'bullet', 'link', 'image', 'video',
  ];

  return (
    <div className='container p-5'>
      <form>
        <div className="form-group">
          <label>Title</label>
          <input type="text" onChange={(e) => setTitle(e.target.value)} className="form-control" placeholder="Title" />
        </div>

        <div className="form-group">
          <label>Body</label>
          <ReactQuill
            theme="snow"
            value={body}
            onChange={(value) =>{ setBody(value); console.log(value)}}
            modules={modules}
            formats={formats}
            placeholder="Write something amazing..."
          />
        </div>

        <Link className='btn btn-primary' to={`/HomePage/${userId}`} onClick={clicked}>
          Publish
        </Link>
      </form>
    </div>
  );
}

export default CreatePost;
