import React, { useState, useEffect } from 'react';

function SuggestionCompo() {
    const [suggestions, setSuggestions] = useState([]);
    const [users, setUsers] = useState([]);
    const [posts, setPosts] = useState([]);
  
    useEffect(() => {
      const fetchUsers = async () => {
        try {
            const usersResponse = await fetch('https://mern.up.railway.app/getusers');
            const usersData = await usersResponse.json();
            setUsers(usersData);
        } catch (error) {
          console.error('Error fetching users:', error);
        }
      };
  
      const fetchPosts = async () => {
        try {
            const response = await fetch('https://mern.up.railway.app/');
            if (!response.ok) {
                throw new Error(`HTTP error! Status: ${response.status}`);
            }
            const fetchedPosts = await response.json();
            setPosts(fetchedPosts);
        } catch (error) {
          console.error('Error fetching posts:', error);
        }
      };
  
      const fetchSuggestions = async () => {
        try {
          const response = await fetch('https://mern.up.railway.app/suggestions');
          if (!response.ok) {
            throw new Error(`HTTP error! Status: ${response.status}`);
          }
          const suggestionData = await response.json();
          setSuggestions(suggestionData);
        } catch (error) {
          console.error('Error fetching suggestions:', error);
        }
      };
  
      fetchUsers();
      fetchPosts();
      fetchSuggestions();
    }, []);

  const handleDeleteSuggestion = async (sugId) => {
    try {
      // Make an API call to delete suggestion
      await fetch(`https://mern.up.railway.app/suggestions/${sugId}`, {
        method: 'DELETE',
        headers: {
          'Content-Type': 'application/json',
        },
      });

      // Update the local state after successful deletion
      setSuggestions((prevSuggestions) => prevSuggestions.filter((suggestion) => suggestion.sug_id !== sugId));
    } catch (error) {
      console.error('Error deleting suggestion:', error);
    }
  };

  const handleUpdateBody = async (sugId, newBody) => {
    try {
      // Make an API call to update the body of the suggestion
      await fetch(`https://mern.up.railway.app/suggestions/${sugId}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ body: newBody }),
      });

      // Update the local state after successful update
      setSuggestions((prevSuggestions) =>
        prevSuggestions.map((suggestion) =>
          suggestion.sug_id === sugId ? { ...suggestion, body: newBody } : suggestion
        )
      );
    } catch (error) {
      console.error('Error updating suggestion body:', error);
    }
  };

  return (
    <div className="container mt-5">
      <h2>Suggestions</h2>
      <table className="table">
        <thead>
          <tr>
            <th>Suggestion ID</th>
            <th>Body</th>
            <th>From</th>
            <th>To</th>
            <th>Post Title</th>
            <th>Status</th>
            <th>Action</th>
          </tr>
        </thead>
        <tbody>
          {suggestions.map((suggestion) => (
            <tr key={suggestion.sug_id}>
              <td>{suggestion.sug_id}</td>
              <td>{suggestion.body}</td>
              <td>{users.find((user) => user.user_id === suggestion.from)?.name}</td>
              <td>{users.find((user) => user.user_id === suggestion.to)?.name}</td>
              <td>{posts.find((post) => post.post_id === suggestion.post_id)?.title}</td>
              <td>{suggestion.status}</td>
              <td>
                <button
                  className="btn btn-danger me-2"
                  onClick={() => handleDeleteSuggestion(suggestion.sug_id)}
                >
                  Delete
                </button>
                <button
                  className="btn btn-primary"
                  onClick={() => {
                    const newBody = prompt('Enter the new body:');
                    if (newBody !== null) {
                      handleUpdateBody(suggestion.sug_id, newBody);
                    }
                  }}
                >
                  Update Body
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}

export default SuggestionCompo;
