import React from 'react';
import { Link, useParams } from 'react-router-dom';

function TopBar(props) {
  //const location = useLocation();
  const {id} = useParams();
    // location.pathname.split('/HomePage/')[1] ||
    // location.pathname.split('/Aboutus/')[1] ||
    // location.pathname.split('/ContactUs/')[1] ||
    // location.pathname.split('/EditProfile/')[1]||
    // location.pathname.split('/Suggestion/')[1]
    // ;

  return (
    <div>
      <nav className="navbar navbar-expand-lg navbar-light bg-light sticky-top">
        <Link className="navbar-brand" to={`/HomePage/${id}`}>
          BlogMingle
        </Link>
        <button
          className="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarNav"
          aria-controls="navbarNav"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarNav">
          <ul className="navbar-nav mx-2 justify-content-center">
            <li className="nav-item">
              <Link className="nav-link" to={`/HomePage/${id}`}>
                Home <span className="sr-only">(current)</span>
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to={`/Suggestion/${id}`}>
                My Suggestions
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to={`/Aboutus/${id}`}>
                About
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to={`/ContactUs/${id}`}>
                Contact us
              </Link>
            </li>
          </ul>
          <ul className="navbar-nav ml-auto mx-5">
          <li className="nav-item">
              <Link
                className="nav-link"
                to={`/EditProfile/${id}`}
                style={{
                  background: 'blue',
                  borderRadius: '2vh',
                  color: 'white',
                  transition: 'background 0.3s',
                  marginRight:'5vh'
                }}
                onMouseOver={(e) => (e.target.style.background = 'darkblue')}
                onMouseOut={(e) => (e.target.style.background = 'blue')}
              >
                Edit Profile
              </Link>
            </li>
            <li className="nav-item">
              <Link
                className="nav-link"
                to={`/`}
                style={{
                  background: 'red',
                  borderRadius: '2vh',
                  color: 'white',
                  transition: 'background 0.3s',
                }}
                onMouseOver={(e) => (e.target.style.background = 'darkred')}
                onMouseOut={(e) => (e.target.style.background = 'red')}
              >
                Logout
              </Link>
            </li>
           
          </ul>
        </div>
      </nav>
      <link
        rel="stylesheet"
        href="https://cdn.jsdelivr.net/npm/bootstrap@4.0.0/dist/css/bootstrap.min.css"
        integrity="sha384-Gn5384xqQ1aoWXA+058RXPxPg6fy4IWvTNh0E263XmFcJlSAwiGgFAW/dAiS6JXm"
        crossOrigin="anonymous"
      ></link>
    </div>
  );
}

export default TopBar;
