import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom';

function AboutUs() {
    
  const [personimageUrl, setpersonImageUrl] = useState();
  const onstart=async()=>{
    await fetch('https://mern.up.railway.app/about_us', {
      method: 'POST',
    })
      .then((response) => response.blob())
      .then((blob) => {
        setpersonImageUrl(URL.createObjectURL(blob));
        // Use imageUrl as the source for an <img> tag or however you need
      })
      .catch((error) => console.error('Error fetching image:', error));
   
  }
  useEffect(() => {
    // Fetch data of users from the API when the component mounts
    onstart();
   }, []);
    return (
        <section className="about-us">
          <div className='container'>
            <div className="about">
                <img src={personimageUrl} className="pic"></img>
                <div className="text">
                    <h2>About Us</h2>
                    <h5>Welcome to <span>BlogMingle</span></h5>
                    <p>where creativity meets expression. Dive into a dynamic community for content writers and bloggers. 
                        Discover diverse ideas, compelling stories, and a supportive space for your unique voice. Join us in celebrating the art of wordsmithing and storytelling. Your journey starts here, where every word finds its resonance</p>
                    <div className="data">
                        <Link className='hire' to={"/ContactUs"}>Contact Us</Link>
                    </div>
                </div>
            </div>
            </div>
        </section>
    )
}

export default AboutUs