import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';

function PostsCompo() {
    const [posts, setPosts] = useState([]);

    // Fetch posts from the API
    const fetchPosts = async () => {
        try {
            const response = await fetch('https://mern.up.railway.app/');
            if (!response.ok) {
                throw new Error(`HTTP error! Status: ${response.status}`);
            }
            const fetchedPosts = await response.json();
            setPosts(fetchedPosts);
        } catch (error) {
            console.error('Error fetching posts:', error);
        }
    };

    useEffect(() => {
        fetchPosts();
    }, [posts]); // Run only once on component mount
    const handleDelete = async (postId) => {
        try {
            // Make API call to delete post
            // Replace 'http://your-api-url/deletepost' with the actual URL of your API endpoint
            const response = await fetch(`https://mern.up.railway.app/deletepost/${postId}`, {
                method: 'DELETE',
                headers: {
                    'Content-Type': 'application/json',
                    // You may need to include additional headers, such as authorization headers
                },
            });

            if (!response.ok) {
                throw new Error(`HTTP error! Status: ${response.status}`);
            }

            // Update the local state after successful deletion
            setPosts((prevPosts) => prevPosts.filter((post) => post.post_id !== postId));

        } catch (error) {
            console.error('Error deleting post:', error);
        }
    };
    const handleUnpublish = async (postId) => {
        try {
            const post = posts.find((post) => post.post_id === postId);
            if (post && post.status === 'unpublished') {
                console.warn('Post is already unpublished.');
                return;
            }

            await fetch(`https://mern.up.railway.app/posts/${postId}/unpublish`, {
                method: 'PUT',
            });

            setPosts((prevPosts) =>
                prevPosts.map((post) =>
                    post.post_id === postId ? { ...post, status: 'unpublished' } : post
                )
            );
        } catch (error) {
            console.error('Error updating post status:', error);
        }
    };

    const approvePost = async (postId) => {
        try {
            await fetch(`https://mern.up.railway.app/posts/${postId}/approve`, {
                method: 'POST',
            });

            setPosts((prevPosts) =>
                prevPosts.map((post) =>
                    post.post_id === postId ? { ...post, status: 'approved' } : post
                )
            );
        } catch (error) {
            console.error('Error approving post:', error);
        }
    };

    return (
        <div className="container mt-5">
            <h2>Posts</h2>
            <div className='d-flex justify-content-end mb-3'>
                <Link className="btn btn-success" to={`/Admin/CreatePost`}>
                    Create Post
                </Link>
            </div>
            {posts.map((post) => (
                <div key={post.post_id} className="card mb-3">
                    <div className="card-body">
                        <h5 className="card-title">{post.title}</h5>
                        <p className="card-text" dangerouslySetInnerHTML={{ __html: post.body }}></p>
                        <p className="card-text">
                            <small className="text-muted">{post.date} - {post.time}</small>
                        </p>
                        <p className="card-text">
                            <small className="text-muted">Status: {post.status}</small>
                        </p>
                        <Link className='btn btn-primary me-2' to={`/Admin/EditPost/${post.post_id}`}>Edit</Link>
                        {post.status === 'approved' && (
                            <button
                                type="button"
                                className="btn btn-warning me-2"
                                onClick={() => handleUnpublish(post.post_id)}
                            >
                                Unpublish
                            </button>
                        )}

                        {(post.status === 'pending' || post.status === 'unpublished') && (
                            <button
                                type="button"
                                className="btn btn-success me-2"
                                onClick={() => approvePost(post.post_id)}
                            >
                                Approve
                            </button>
                        )}

                        <button
                            type="button"
                            className="btn btn-danger"
                            onClick={() => handleDelete(post.post_id)}
                        >
                            Delete
                        </button>
                    </div>
                </div>
            ))}
        </div>
    );
}

export default PostsCompo;


