import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { Modal, Button } from 'react-bootstrap';

function MySuggestion() {
  const { id } = useParams();
  const [yoursuggestions, setYourSuggestions] = useState([]);
  const [givensuggestions, setGivenSuggestions] = useState([]);
  const [showReplyModal, setShowReplyModal] = useState(false);
  const [replyText, setReplyText] = useState('');
  const [replyingSuggestionID, setreplyingSuggestionID] = useState(null);
  const [checkSeeReply, setcheckSeeReply] = useState(false);
  const [replies, setReplies] = useState({});
  useEffect(() => {
    const fetchData = async () => {
      try {
        const yoursuggestionsresponse = await fetch(`https://mern.up.railway.app/mysuggestions/${id}`);
        const givensuggestionsresponse = await fetch(`https://mern.up.railway.app/givensuggestions/${id}`);
        const data = await yoursuggestionsresponse.json();
        const data1 = await givensuggestionsresponse.json();
        // Check if the component is still mounted before updating state
        if (mounted) {
          setYourSuggestions(data);
          setGivenSuggestions(data1)
        }
      } catch (error) {
        console.error("Error fetching suggestions:", error);
      }
    };

    let mounted = true; // Variable to track component mounting status

    fetchData();

    // Cleanup function to set mounted to false when the component is unmounted
    return () => {
      mounted = false;
    };
  }, [id]);

  const handleReject = async (sug_id) => {
    try {
      const response = await fetch(`https://mern.up.railway.app/rejectsuggestion/${sug_id}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ status: 'rejected' }),
      });
  
      if (response.ok) {
        // If the server updates successfully, update the status in the state
        setGivenSuggestions((prevSuggestions) => {
          return prevSuggestions.map(suggestion =>
            suggestion.sug_id === sug_id ? { ...suggestion, status: 'rejected' } : suggestion
          );
        });
      } else {
        console.error('Failed to update suggestion status:', response.status);
      }
    } catch (error) {
      console.error('Error updating suggestion status:', error);
    }
  };
  

  const closeReplyModal = () => {
    setShowReplyModal(false);
    setreplyingSuggestionID(null);
    setReplyText('');
  };

  // Function to handle reply suggestion submission
  const handleReplySubmit = async () => {
    try {
      const response = await fetch(`https://mern.up.railway.app/replysuggestion/${replyingSuggestionID}`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          body: replyText,
          sug_id: replyingSuggestionID,
          from: id
        }),
      });

      if (response.ok) {
        // Handle success (e.g., show a success message)
        alert('Reply submitted successfully');
      } else {
        console.error('Failed to submit reply:', response.status);
      }
    } catch (error) {
      console.error('Error submitting reply:', error);
    }

    // Close the reply modal after submitting
    closeReplyModal();
  };
  const openReplyModal = (commentId) => {
    setreplyingSuggestionID(commentId);
    setShowReplyModal(true);
  };

  //Function to see replyies
  const handleSeeReplies = async () => {
    try {
      const response = await fetch(`https://mern.up.railway.app/seereplies/${id}`);
      const repliesArray = await response.json();
      if (repliesArray.length <= 0) {
        alert("No replies");
      } else {
        setcheckSeeReply(true);
        const usersArray = await Promise.all(repliesArray.map(suggestion => fetch(`https://mern.up.railway.app/users/${suggestion.from}`).then(res => res.json())));
        setReplies(repliesArray.map((suggestion, index) => ({ ...suggestion, user: usersArray[index] })));
      }
    } catch (error) {
      console.error("Error fetching replies:", error);
    }
  };

  const closeReplies=()=>{
    setcheckSeeReply(false);
  }
  return (
    <div className='container my-4'>

      {/* {Modal to reply suggestions} */}
      <Modal show={showReplyModal} onHide={closeReplyModal}>
        <Modal.Header closeButton>
          <Modal.Title>Reply to Suggestion</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <textarea
            className="form-control"
            rows="2"
            placeholder='Enter Reply'
            value={replyText}
            onChange={(e) => setReplyText(e.target.value)}
          ></textarea>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={closeReplyModal}>
            Close
          </Button>
          <Button variant="primary" onClick={handleReplySubmit}>
            Submit Reply
          </Button>
        </Modal.Footer>
      </Modal>

      <h3>Given Suggestions to other post</h3>
      {yoursuggestions.length > 0 ? (
        <table className="table">
          <thead>
            <tr>
              <th scope="col" style={{ width: '10%' }}>Serial</th>
              <th scope="col">Body</th>
            </tr>
          </thead>
          <tbody>
            {yoursuggestions.map((suggestion, index) => (
              <tr key={index}>
                <th scope="row">{index + 1}</th>
                <td>{suggestion.body}</td>
              </tr>
            ))}
          </tbody>
        </table>
      ) : (
        <p>You haven't given any suggestions to anyone.</p>
      )}

      <h3 className='mt-5'>Others Give You Suggestions</h3>
      {givensuggestions.length > 0 ? (
        <table className="table">
          <thead>
            <tr>
              <th scope="col" style={{ width: '10%' }}>Serial</th>
              <th scope="col">Body</th>
              <th scope="col" style={{ width: '20%' }}>Action</th>
            </tr>
          </thead>
          <tbody>
            {givensuggestions.map((suggestion, index) => (
              <tr key={index} >
                <th scope="row">{index + 1}</th>
                <td>{suggestion.body}</td>
                <td>
                  {suggestion.status === 'rejected' ? (
                    <span style={{ color: 'red' }}>Rejected</span>
                  ) : (<div>

                    <button className='btn btn-secondary mr-2' onClick={() => openReplyModal(suggestion.sug_id)}>Reply</button>
                    <button className='btn btn-danger' style={{ height: 35 }} onClick={() => handleReject(suggestion.sug_id)}>Reject</button>

                  </div>
                  )
                  }
                </td></tr>
            ))}

          </tbody>
        </table>
      ) : (
        <p>No one has gien any suggestion</p>
      )}
      {/* See Replies */}
      {(checkSeeReply === false) ?
        <div className="d-flex justify-content-end mb-5">

          <button className='btn btn-secondary' onClick={handleSeeReplies}>See Replies</button>

        </div> :
        <div className='container p-5'>
          <div className='d-flex justify-content-end'>
          <button className='btn btn-info ' onClick={closeReplies}>close Replies</button>
          
          </div>
          <br /><br /><br />
          <label>Replies from other users</label>
          {replies.length > 0 ? (
            <table className="table">
              <thead>
                <tr>
                  <th scope="col" style={{ width: '10%' }}>Serial</th>
                  <th scope="col">Body</th>
                  <th scope="col">From</th>
                </tr>
              </thead>
              <tbody>
                {replies.map((suggestion, index) => (
                  <tr key={index}>
                    <th scope="row">{index + 1}</th>
                    <td>{suggestion.body}</td>
                    <td>{suggestion.user.name}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          ) : (
            <p>No Replies</p>
          )}

        </div>
      }
    </div>
  );
}

export default MySuggestion;
