import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';

function ModeratorPostPage() {
    const [post, setPost] = useState({});
    const [isApproved, setIsApproved] = useState(false);
    const { id } = useParams();

    useEffect(() => {
        const fetchData = async () => {
            try {

                const response = await fetch('https://mern.up.railway.app/');
                const posts = await response.json();
                const foundPost = posts.find((p) => parseInt(p.post_id) === parseInt(id));

                setPost(foundPost || {});
                setIsApproved(foundPost.status === 'approved');
            } catch (error) {
                console.error('Error fetching data:', error);
            }
        };

        fetchData();
    }, [id]);

    const approvePost = async () => {
        try {
            await fetch(`https://mern.up.railway.app/posts/${id}/approve`, {
                method: 'POST',
            });
            setIsApproved(true);
        } catch (error) {
            console.error('Error approving post:', error);
        }
    };

    return (
        <div className='container p-5'>
            <div className="col-md-9">
                {Object.keys(post).length > 0 ? (
                    <article className="blog-post">
                        <h2 className="display-5 link-body-emphasis mb-1">{post.title}</h2>
                        <div className="d-flex justify-content-end">
                            {!isApproved && (
                                <button className='btn btn-success' onClick={approvePost}>
                                    Approve
                                </button>
                            )}
                        </div>
                        <p>Status: {isApproved ? 'Approved' : post.status}</p>
                        <p className="blog-post-meta">
                            PublishDate : {post.date} PublishTime : {post.time}
                        </p>
                        <p dangerouslySetInnerHTML={{ __html: post.body }}></p>
                      
                    </article>
                ) : (
                    <p>No post found with the given ID.</p>
                )}
            </div>
        </div>
    );
}

export default ModeratorPostPage;

