import React, { useState } from "react";
import { useNavigate } from "react-router-dom";

export const Register = (props) => {
    const [email, setEmail] = useState('');
    const [pass, setPass] = useState('');
    const [name, setName] = useState('');
    const nav = useNavigate();
    const handleSubmit = async (e) => {
        e.preventDefault();
        const response = await fetch('https://mern.up.railway.app/createuser', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                name: name,
                email: email,
                pass: pass
            })
        });

        if (response.ok) {
            // Successful registration, navigate to the HomePage
            nav('/HomePage');
        } else {
            const data = await response.json();

            if (response.status === 400 && data.error === 'Email already exists') {
                // Display an alert for duplicate email
                alert('Email already exists. Please enter a unique email.');
            } else {
                // Display an alert for other server errors
                alert('Server error');
            }
        }
    }


    return (
        <div className="auth-form-container">
            <h2>Register</h2>
            <form className="register-form" onSubmit={handleSubmit}>
                <label htmlFor="name">Full name</label>
                <input value={name} name="name" onChange={(e) => setName(e.target.value)} id="name" placeholder="full Name" />
                <label htmlFor="email">email</label>
                <input value={email} onChange={(e) => setEmail(e.target.value)} type="email" placeholder="youremail@gmail.com" id="email" name="email" />
                <label htmlFor="password">password</label>
                <input value={pass} onChange={(e) => setPass(e.target.value)} type="password" placeholder="********" id="password" name="password" />
                <button type="submit">Register</button>
            </form>
            <button className="link-btn" onClick={() => props.onFormSwitch('login')}>Already have an account? Login here.</button>
        </div>
    )
}
