import React from 'react'
import { Link } from 'react-router-dom'

function Navbar() {
    return (
        <nav className="navbar navbar-expand-lg navbar-light bg-light">
            <a className="navbar-brand" href="#">Admin</a>
            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"></span>
            </button>
            <div className="collapse navbar-collapse" id="navbarNav">
                <ul className="navbar-nav">
                    <li className="nav-item active">
                        <Link className="nav-link" href="#" to={'/Admin/users'}>Users </Link>
                    </li>
                    <li className="nav-item">
                        <Link className="nav-link" href="" to={'/Admin/Posts'} >Posts</Link>
                    </li>
                    <li className="nav-item">
                        <Link className="nav-link" href="#" to={'/Admin/Comments'}>Comments</Link>
                    </li>
                    <li className="nav-item">
                        <Link className="nav-link" href="" to={'/Admin/Suggestions'}>Suggestions</Link>
                    </li>
                    <li className="nav-item">
                        <Link className="nav-link" href="" to={'/Admin/Likes'}>Likes</Link>
                    </li>
                    <ul className="navbar-nav ml-auto mx-5">
                        <li className="nav-item">
                            <Link
                                className="nav-link"
                                to={`/`}
                                style={{
                                    background: 'red',
                                    borderRadius: '2vh',
                                    color: 'white',
                                    transition: 'background 0.3s',
                                }}
                                onMouseOver={(e) => (e.target.style.background = 'darkred')}
                                onMouseOut={(e) => (e.target.style.background = 'red')}
                            >
                                Logout
                            </Link>
                        </li>

                    </ul>
                </ul>
            </div>
            <link href="https://cdn.jsdelivr.net/npm/bootstrap@5.3.2/dist/css/bootstrap.min.css" rel="stylesheet" integrity="sha384-T3c6CoIi6uLrA9TneNEoa7RxnatzjcDSCmG1MXxSR1GAsXEV/Dwwykc2MPK8M2HN" crossOrigin="anonymous"></link>

        </nav>
    )
}

export default Navbar