import React, { useState, useEffect } from 'react';

function Likes() {
  const [postLikes, setPostLikes] = useState([]);
  const [commentLikes, setCommentLikes] = useState([]);
  const [users, setUsers] = useState([]);
  const [posts, setPosts] = useState([]);
  const [comments, setComments] = useState([]);

  useEffect(() => {
    // Fetch post likes
    fetch('https://mern.up.railway.app/postlikes')
      .then((response) => response.json())
      .then((data) => setPostLikes(data))
      .catch((error) => console.error('Error fetching post likes:', error));

    // Fetch comment likes
    fetch('https://mern.up.railway.app/commentlikes')
      .then((response) => response.json())
      .then((data) => setCommentLikes(data))
      .catch((error) => console.error('Error fetching comment likes:', error));

    // Fetch users
    fetch('https://mern.up.railway.app/getusers')
      .then((response) => response.json())
      .then((data) => setUsers(data))
      .catch((error) => console.error('Error fetching users:', error));

    // Fetch posts
    fetch('https://mern.up.railway.app/')
      .then((response) => response.json())
      .then((data) => setPosts(data))
      .catch((error) => console.error('Error fetching posts:', error));

    // Fetch comments
    fetch('https://mern.up.railway.app/comments')
      .then((response) => response.json())
      .then((data) => setComments(data))
      .catch((error) => console.error('Error fetching comments:', error));
  }, []);

  return (
    <div className="container mt-5">
      <h2>Likes</h2>
      <div>
        <h3>Post Likes</h3>
        <table className="table">
          <thead>
            <tr>
              <th>User Name</th>
              <th>Post Title</th>
            </tr>
          </thead>
          <tbody>
            {postLikes.map((postLike) => (
              <tr key={postLike.post_like_id}>
                <td>{users.find((user) => user.user_id === postLike.user_id)?.name}</td>
                <td>{posts.find((post) => post.post_id === postLike.post_id)?.title}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      <div>
        <h3>Comment Likes</h3>
        <table className="table">
          <thead>
            <tr>
              <th>User Name</th>
              <th>Comment Body</th>
            </tr>
          </thead>
          <tbody>
            {commentLikes.map((commentLike) => (
              <tr key={commentLike.comment_like_id}>
                <td>{users.find((user) => user.user_id === commentLike.user_id)?.name}</td>
                <td>{comments.find((comment) => comment.comment_id === commentLike.comment_id)?.body}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
}

export default Likes;
