import React, { useEffect, useState } from 'react';
import { Link, useParams } from 'react-router-dom';
import { Dropdown, Modal, Button } from 'react-bootstrap'; // Make sure to import Modal and Button from react-bootstrap
import SuggestionComponent from './SuggestionComponent';


function PostPage() {
  const [post, setPost] = useState({});
  const { post_id, id } = useParams();
  const [comments, setComments] = useState([]);
  const [isOpenArray, setIsOpenArray] = useState([]);
  const cleanedParam = parseInt(post_id ? post_id.replace(':', '') : '');
  const [text, setText] = useState('');
  const [showReportPostModal, setshowReportPostModal] = useState(false);
  const [showReportModal, setShowReportModal] = useState(false);
  const [commentReportID, setcommentReportID] = useState();
  const [liked, setLiked] = useState(false);
  const [likedCommentsArray, setlikedCommentsArray] = useState({});
  const [showReplyModal, setShowReplyModal] = useState(false);
  const [replyText, setReplyText] = useState('');
  const [replyingCommentId, setReplyingCommentId] = useState(null);
  const [replies, setreplies] = useState([]);
  useEffect(() => {
    const fetchData = async () => {
      try {
        const likedcommentResponse = await fetch('https://mern.up.railway.app/likedComment');
        const likedcomments = await likedcommentResponse.json();
        setlikedCommentsArray(likedcomments);
        const response = await fetch('https://mern.up.railway.app/');
        const posts = await response.json();
        const commentsResponse = await fetch('https://mern.up.railway.app/comments');
        const comments = await commentsResponse.json();

        const replydata = await fetch('https://mern.up.railway.app/getreplies');
        const repliesarray = await replydata.json();
        const foundPost = posts.find((p) => parseInt(p.post_id) === parseInt(cleanedParam));
        setreplies(repliesarray || {})
        setPost(foundPost || {});
        setComments(comments || {});
        setIsOpenArray(new Array(comments.length).fill(false)); // Initialize isOpenArray
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    };

    fetchData();
  }, [post_id, id]);

  // Function To add a comment
  const addComment = async () => {
    const rawData = new Date();
    const date = rawData.getDate() + '/' + (rawData.getMonth() + 1) + '/' + rawData.getFullYear(); // Note: Months are zero-based
    const time = rawData.getHours() + ':' + rawData.getMinutes();
    const response = await fetch('https://mern.up.railway.app/createcomment', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        body: text,
        post_id: cleanedParam,
        date: date,
        time: time,
        id: null
      }),
    });
    setText('');
    if (response.ok) {
      alert("Comment is Added Please Reload The Page");
    }
    else {
      alert("Try Re-entring the comment");
    }
  };

  // Function to control which comment option is clicked 
  const handleToggle = (index) => {
    // Update the isOpen state for the clicked row
    const updatedIsOpenArray = [...isOpenArray];
    updatedIsOpenArray[index] = !updatedIsOpenArray[index];
    setIsOpenArray(updatedIsOpenArray);
  };


  //  Function to control Post Report 
  const handlePostReportSubmit = async () => {
    try {
      const selected = document.getElementById('selected');
      const response = await fetch(`https://mern.up.railway.app/reportpost/${post.post_id}`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          reason: selected.value,
        }),
      });

      if (response.ok) {
        alert('Post reported successfully');
      } else {
        console.error('Failed to report post:', response.status);
      }
    } catch (error) {
      console.error('Error reporting post:', error);
    }
    handleclosePostModal();
  };
  const handleclosePostModal = () => {
    setshowReportPostModal(false);
  }
  const reportPost = () => {
    setshowReportPostModal(true);
  };

  // Functions to control comment Report
  const handleCloseReportModal = () => {
    // Close the report modal
    setShowReportModal(false);
  };
  const handleReportSubmit = async () => {
    try {
      const selected = document.getElementById('selectedoption');
      const response = await fetch(`https://mern.up.railway.app/reportcomment/${commentReportID}`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          reason: selected.value,
        }),
      });

      if (response.ok) {
        // Handle success (e.g., show a success message)
        alert('comment reported successfully');
      } else {
        console.error('Failed to report comment:', response.status);
      }
    } catch (error) {
      console.error('Error reporting post:', error);
    }

    // Close the report modal after submitting
    handleCloseReportModal();
  };
  const reportcomment = (rpid) => {
    return () => {
      setShowReportModal(true);
      setcommentReportID(rpid);
    };
  };

  //Function for like comment 
  const handleLikeClick = async (commentId) => {
    if (!liked) {
      try {
        // Make API call to update like status on the server
        const response = await fetch(`https://mern.up.railway.app/likecomment/${commentId}/${id}`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({ liked: true }),
        });

        if (response.ok) {
          // If the server updates successfully, change the button appearance
          setLiked(true);
        } else {
          console.error('Failed to update like status:', response.status);
        }
      } catch (error) {
        console.error('Error updating like status:', error);
      }
    }
  };

  const openReplyModal = (commentId) => {
    setReplyingCommentId(commentId);
    setShowReplyModal(true);
  };

  // Function to close reply modal
  const closeReplyModal = () => {
    setShowReplyModal(false);
    setReplyingCommentId(null);
    setReplyText('');
  };

  // Function to handle reply comment submission
  const handleReplySubmit = async () => {
    try {
      const response = await fetch(`https://mern.up.railway.app/replycomment/${replyingCommentId}`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          body: replyText,
          id: id, // Replace with the actual user ID
        }),
      });

      if (response.ok) {
        // Handle success (e.g., show a success message)
        alert('Reply submitted successfully');
      } else {
        console.error('Failed to submit reply:', response.status);
      }
    } catch (error) {
      console.error('Error submitting reply:', error);
    }

    // Close the reply modal after submitting
    closeReplyModal();
  };

  return (
    <div className='container p-5'>

      <div className="col-md-9">
        {Object.keys(post).length > 0 ? (
          <article className="blog-post">
            <h2 className="display-5 link-body-emphasis mb-1">{post.title}</h2>
            <div className="d-flex justify-content-end">

              <button className='btn btn-secondary' onClick={reportPost}>Report</button>
            </div>
            <p>Status:{post.status}</p>
            <p className="blog-post-meta">PublishDate : {post.date} PublishTime : {post.time}</p>
            <p dangerouslySetInnerHTML={{ __html: post.body }}></p>
            <hr />
            <br />
            {/* Report Post Modal */}
            <Modal show={showReportPostModal} onHide={handleclosePostModal}>
              <Modal.Header closeButton>
                <Modal.Title>Report Post</Modal.Title>
              </Modal.Header>
              <Modal.Body>
                <p>Select a reason for reporting this post:</p>
                {/* Add your selection options here */}
                <select id='selected'>
                  <option value="spam" >Spam</option>
                  <option value="inappropriate">Inappropriate Content</option>
                  {/* Add more options as needed */}
                </select>
              </Modal.Body>
              <Modal.Footer>
                <Button variant="secondary" onClick={handleclosePostModal}>
                  Close
                </Button>
                <Button variant="primary" onClick={() => handlePostReportSubmit()}>
                  Submit Report
                </Button>
              </Modal.Footer>
            </Modal>

            {/* Report comment Modal */}
            <Modal show={showReportModal} onHide={handleCloseReportModal}>
              <Modal.Header closeButton>
                <Modal.Title>Report Comment</Modal.Title>
              </Modal.Header>
              <Modal.Body>
                <p>Select a reason for reporting this Comment:</p>
                {/* Add your selection options here */}
                <select id='selectedoption'>
                  <option value="spam">Spam</option>
                  <option value="inappropriate">Inappropriate Content</option>
                  {/* Add more options as needed */}
                </select>
              </Modal.Body>
              <Modal.Footer>
                <Button variant="secondary" onClick={handleCloseReportModal}>
                  Close
                </Button>
                <Button variant="primary" onClick={() => handleReportSubmit('selectedReason')}>
                  Submit Report
                </Button>
              </Modal.Footer>
            </Modal>

            {/* Reply Comment Modal */}
            <Modal show={showReplyModal} onHide={closeReplyModal}>
              <Modal.Header closeButton>
                <Modal.Title>Reply to Comment</Modal.Title>
              </Modal.Header>
              <Modal.Body>
                <textarea
                  className="form-control"
                  rows="2"
                  placeholder='Enter Reply'
                  value={replyText}
                  onChange={(e) => setReplyText(e.target.value)}
                ></textarea>
              </Modal.Body>
              <Modal.Footer>
                <Button variant="secondary" onClick={closeReplyModal}>
                  Close
                </Button>
                <Button variant="primary" onClick={handleReplySubmit}>
                  Submit Reply
                </Button>
              </Modal.Footer>
            </Modal>
            <h4>Comments</h4>
            <div>
              <table className="table table-borderless">
                <thead>
                  <tr>
                    <th scope="col">#</th>
                    <th scope="col">Comments</th>
                    <th scope="col">Replies</th> {/* Add a new column for replies */}
                    <th>Actions</th>
                  </tr>
                </thead>
                <tbody>
                  {comments
                    .filter(comment => cleanedParam === parseInt(comment.post_id))
                    .map((comment, index) => (
                      <tr key={index}>
                        <th scope="row">{index + 1}</th>
                        <td>{comment.body}</td>
                        <td>
                          {/* Display replies in the new column */}
                          {replies.filter(reply => reply.realcomment_id === comment.comment_id).map((filteredReply, replyIndex) => (
                            <div key={replyIndex}>{filteredReply.body}</div>
                          ))}
                        </td>
                        <td>
                          <Dropdown show={isOpenArray[index]} onToggle={() => handleToggle(index)} className='m-1'>
                            <Dropdown.Toggle variant="text-info" id={`dropdown-basic-${index}`} className='test'></Dropdown.Toggle>
                            <Dropdown.Menu>
                              <Dropdown.Item href="#" style={{ color: 'blueviolet' }} onClick={() => handleLikeClick(comment.comment_id)} disabled={likedCommentsArray.some(c => c.comment_id === comment.comment_id)}>
                                {(likedCommentsArray.find((c) => parseInt(c.comment_id) === parseInt(comment.comment_id))) ? "liked" : "like"}
                              </Dropdown.Item>
                              <Dropdown.Item style={{ color: 'red' }} onClick={() => openReplyModal(comment.comment_id)}>Reply</Dropdown.Item>
                              <Dropdown.Item href="#" style={{ color: 'yellowgreen' }} onClick={reportcomment(comment.comment_id)}>Report</Dropdown.Item>
                            </Dropdown.Menu>
                          </Dropdown>
                        </td>
                      </tr>
                    ))}
                </tbody>
              </table>
            </div>
            <textarea className="form-control" rows="2" placeholder='Enter Comment' value={text} onChange={(e) => { setText(e.target.value) }}></textarea>
            <Link className='btn btn-primary' onClick={addComment}>
              Add
            </Link>
            <br />
            <br /><br />
            <SuggestionComponent id={id} post_id={post_id} />
          </article>
        ) : (
          <p>No post found with the given ID.</p>
        )}
      </div>
    </div>
  );
}

export default PostPage;



