import React, { useState } from "react";
import './App.css';
import { Login } from "./components/Login";
import { Register } from "./components/Register";
import { BrowserRouter, Link, Route, Routes } from "react-router-dom";
import HomePage from "./components/HomePage";
import TopBar from "./components/TopBar";
import "./components/style.css";
import AboutUs from "./components/AboutUs";
import ContactUs from "./components/ContactUs";
import CreatePost from "./components/CreatePost";
import PostPage from "./components/PostPage";
import EditProfile from "./components/EditProfile";
import MySuggestion from "./components/MySuggestion";
import { ModeratorLogin } from "./components/ModeratorLogin";
import ModeratorHomepage from "./components/ModeratorHomepage";
import Header from "./components/Header";
import ModeratorPostPage from "./components/ModeratorPostPage";
import UsersCompo from "./components/Admin/users/UsersCompo";
import UpdateUser from "./components/Admin/users/UpdateUser";
import CreateUser from "./components/Admin/users/CreateUser";
import Navbar from "./components/Admin/navbar/Navbar";
import PostsCompo from "./components/Admin/posts/PostsCompo";
import AdminCreatePost from "./components/Admin/posts/AdminCreatePost";
import AdminViewPost from "./components/Admin/posts/AdminViewPost";
import EditPost from "./components/Admin/posts/EditPost";
import SuggestionCompo from "./components/Admin/suggestions/SuggestionCompo";
import Comments from "./components/Admin/comments/Comments";
import Likes from "./components/Admin/Likes/Likes";
import Adminlogin from "./components/Admin/login/Adminlogin";

function App() {
  const [currentForm, setCurrentForm] = useState('login');
  const [authenticated, setAuthenticated] = useState(false);
  const linkStyle = {
    background: 'lightblue',
    color: 'black',
    padding: 10,
    borderRadius: 10,
    fontsize:40,
    fontstyle:'bold',
    marginRight: 50,
    textDecoration: 'none', // Remove default underline
    display: 'inline-block', // Ensure inline display
  };
  const toggleForm = (formName) => {
    setCurrentForm(formName);
  }
  return (
    <BrowserRouter>
      
      <Routes>
      <Route
          path="/"
          element={
            <div style={{ height: 100, width: 700, margin: 300 }}>
              <Link to="/Login" style={linkStyle}>
                Are you User?
              </Link>
              <Link to="/moderator" style={linkStyle}>
                Are you a Moderator?
              </Link>
              <Link to="/Admin" style={linkStyle}>
                Are you a Admin?
              </Link>
              
            </div>
          }
        />
        <Route path="/Login" element={<div className="App">
          {
            currentForm === "login" ? <Login onFormSwitch={toggleForm} /> : <Register onFormSwitch={toggleForm} />
          }
        </div>} />
        {/* users routes */}
        <Route path="/HomePage/:id" element={<><TopBar  /><HomePage /></>} />
        <Route path="/AboutUs/:id" element={<><TopBar /><AboutUs /></>} />
        <Route path="/ContactUs/:id" element={<><TopBar /><ContactUs /></>} />
        <Route path="/CreatePost/:id" element={<><TopBar /><CreatePost /></>} />
        <Route path="/PostPage/:post_id/:id" element={<><TopBar /><PostPage /></>} />
        <Route path="/EditProfile/:id" element={<><TopBar /><EditProfile/></>} />
        <Route path="/Suggestion/:id" element={<><TopBar /><MySuggestion/></>} />
        
        {/* Moderator Routes */}
        <Route path="/moderator" element={<div className="App"><ModeratorLogin/></div>}/>
        <Route path="/dashboard/:id" element={<div><Header/><ModeratorHomepage/></div>}/>
        <Route path="/post/:id" element={<div><Header/><ModeratorPostPage/></div>}/>
        
        <Route path="/Admin" element={<div><Adminlogin/></div>}/>
        <Route path="/Admin/users" element={<div><Navbar/><UsersCompo/></div>}/>
        <Route path="/Admin/UpdateUser/:id" element={<div><Navbar/><UpdateUser/></div>}/>
        <Route path="/Admin/CreateUser" element={<div><Navbar/><CreateUser/></div>}/>
        
        <Route path="/Admin/Posts" element={<div><Navbar/><PostsCompo/></div>}/>
        <Route path="/Admin/CreatePost" element={<div><Navbar/><AdminCreatePost/></div>}/>
        <Route path="/Admin/EditPost/:id" element={<div><Navbar/><EditPost/></div>}/>

        <Route path="/Admin/Suggestions" element={<div><Navbar/><SuggestionCompo/></div>}/>
        <Route path="/Admin/Comments" element={<div><Navbar/><Comments/></div>}/>
        <Route path="/Admin/Likes" element={<div><Navbar/><Likes/></div>}/>
        
      </Routes>
      
    </BrowserRouter>
  );
}

export default App;