import React, { useEffect, useState } from 'react';
import PostCard from './PostCard';
import { Link, useLocation } from 'react-router-dom';

function HomePage(props) {
  const [posts, setPosts] = useState([]);
  const [recentPosts, setRecentPosts] = useState([]);
  const [imageUrl, setImageUrl] = useState();
  const location = useLocation();
  const id = location.pathname.split('/HomePage/')[1];

  const fetchPostsAndImage = async () => {
    try {
      const postData = await fetch('https://mern.up.railway.app/').then((response) => response.json());
      setPosts(postData);
      console.log("error")
      const imageBlob = await fetch('https://mern.up.railway.app/header-image', { method: 'POST' }).then(
        (response) => response.blob()
      );
      setImageUrl(URL.createObjectURL(imageBlob));
    } catch (error) {
      console.error('Error fetching data or image:', error);
    }
  };

  useEffect(() => {
    fetchPostsAndImage();
  }, []);
  useEffect(() => {

    // Sort posts based on date and time (assuming date and time are strings)
    const sortedPosts = [...posts].sort((a, b) => {
      const dateA = new Date(`${a.date} ${a.time}`);
      console.log(dateA);
      const dateB = new Date(`${b.date} ${b.time}`);
      return dateB - dateA; // Sort in descending order (most recent first)
    });

    // Display the 4 most recent posts (you can adjust this number as needed)
    const recentPostsSlice = sortedPosts.slice(0, 3);

    setRecentPosts(recentPostsSlice);
  }, [posts]);


  return (
    <>
      <div className="header">
        <img src={imageUrl} className="img-fluid" alt="Blog Header" />
        <div className="overlay-text">Welcome</div>
        <Link className="btn btn-success headerbtn" to={`/CreatePost/${id}`}>
          Create Post
        </Link>
      </div>
      <div className="container">
        <div className="row">
          <div className="col-md-12 mt-4">
            <h3>Recent Posts</h3>
            <div className="d-flex overflow-auto">
              {recentPosts.length > 0 ? (
                recentPosts.map((post) => (
                  <div className="col-md-3 mb-2 mt-2 " key={post.post_id}>
                    <PostCard title={post.title} body={post.body} postid={post.post_id} likecount={post.likecount} />
                  </div>
                ))
              ) : (
                <p>No recent posts found.</p>
              )}
            </div>
          </div>
        </div>
        <div className="row mt-4">
          <div className="col-12">
            <h3>All Posts ({posts.length})</h3>
            <div className="row">
              {posts.length > 0 ? (
                posts.map((post, index) => (
                  // Display all posts for the user (regardless of status) or exclude pending/unpublished for other users
                  (post.user_id === parseInt(id) || post.status !== 'pending' && post.status !== 'unpublished') ? (
                    <div className="col-md-3 mb-2" key={post.post_id}>
                      <PostCard title={post.title} body={post.body} postid={post.post_id} likecount={post.likeCount} />
                    </div>
                  ) : null
                ))
              ) : (
                <p>No posts found.</p>
              )}
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default HomePage;
