import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';

function CreateUser() {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [pass, setPass] = useState('');
  const [status, setStatus] = useState('');
    const nav=useNavigate();
  const handleAddUser = async () => {
    try {
      // Make API call to create user
      const response = await fetch('https://mern.up.railway.app/createuser', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          // You may need to include additional headers, such as authorization headers
        },
        body: JSON.stringify({
          name: name,
          email: email,
          pass: pass,
          status: status,
        })
      });

      if (!response.ok) {
        throw new Error(`HTTP error! Status: ${response.status}`);
      }
      const newUser = await response.json();

      // Handle the response from the server after successful creation
      console.log('User created:', newUser);

      // Optionally, you can reset the input fields after successful creation
      setName('');
      setEmail('');
      setPass('');
      setStatus('');
      nav('/Admin/users');

    } catch (error) {
      // Handle errors during the API call
      console.error('Error creating user:', error);
    }
  };

  return (
    <div className="container mt-5">
      <h2>Create User</h2>

      <form>
        <div className="mb-3">
          <label className="form-label">Name:</label>
          <input type="text" className="form-control" value={name} onChange={(e) => setName(e.target.value)}/>
        </div>

        <div className="mb-3">
          <label className="form-label">Email:</label>
          <input type="text" className="form-control" value={email} onChange={(e) => setEmail(e.target.value)} />
        </div>

        <div className="mb-3">
          <label className="form-label">Password:</label>
          <input type="password" className="form-control" value={pass} onChange={(e) => setPass(e.target.value)} />
        </div>

        <div className="mb-3">
          <label className="form-label">Status:</label>
          <p style={{fontSize:10,color:'red'}}>user or moderator</p>
          <input type="text" className="form-control" value={status} onChange={(e) => setStatus(e.target.value)} />
        </div>

        <button type="button" className="btn btn-primary" onClick={handleAddUser}>
          Add User
        </button>
      </form>
      <link href="https://cdn.jsdelivr.net/npm/bootstrap@5.3.2/dist/css/bootstrap.min.css" rel="stylesheet" integrity="sha384-T3c6CoIi6uLrA9TneNEoa7RxnatzjcDSCmG1MXxSR1GAsXEV/Dwwykc2MPK8M2HN" crossOrigin="anonymous"></link>
   
    </div>
  );
}

export default CreateUser;
