import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
function ModeratorHomepage() {
    const [posts, setPosts] = useState([]);
    const [reportedposts, setReportedPosts] = useState([]);
  
    useEffect(() => {
      const fetchData = async () => {
        try {
          const response = await fetch('https://mern.up.railway.app/'); // Replace with your API endpoint
          const data = await response.json();
  
          const reportedPostresponse = await fetch('https://mern.up.railway.app/reportedposts'); // Replace with your API endpoint
          const report = await reportedPostresponse.json();
          setReportedPosts(report);
          setPosts(data);
        } catch (error) {
          console.error('Error fetching posts:', error);
        }
      };
  
      fetchData();
    }, []);
  
    const handleDelete = async (postId) => {
      try {
        const response = await fetch(`https://mern.up.railway.app/deletepost/${postId}`, {
          method: 'DELETE',
        });
        if (response.ok) {
          setPosts((prevPosts) => prevPosts.filter((post) => post.post_id !== postId));
        } else {
          console.error('Error deleting post:', response.status);
        }
      } catch (error) {
        console.error('Error deleting post:', error);
      }
    };
  
    const handleUnpublish = async (postId) => {
      try {
        // Check if the post is already unpublished
        const post = posts.find((post) => post.post_id === postId);
        if (post && post.status === 'unpublished') {
          console.warn('Post is already unpublished.');
          return;
        }
  
        // Make an API call to update the post status to 'unpublished'
        await fetch(`https://mern.up.railway.app/posts/${postId}/unpublish`, {
          method: 'PUT', // Adjust the HTTP method as per your server implementation
        });
  
        // Update the local state to reflect the status update
        setPosts((prevPosts) =>
          prevPosts.map((post) =>
            post.post_id === postId ? { ...post, status: 'unpublished' } : post
          )
        );
      } catch (error) {
        console.error('Error updating post status:', error);
      }
    };
  
    return (
      <div className='container'>
        <div>
          <h1 className='mt-4'>Pending Posts to approve</h1>
          <table className='table table-bordered'>
            <thead className='thead-dark'>
              <tr>
                <th scope='col'>Index</th>
                <th scope='col'>Title</th>
                <th scope='col'>Status</th>
              </tr>
            </thead>
            <tbody>
              {Array.isArray(posts) && posts.length > 0 ? (
                posts
                  .filter((post) => post.status !== 'approved') // Filter out already approved posts
                  .map((post, index) => (
                    <tr key={post.post_id}>
                      <td>{index + 1}</td>
                      <td>
                        <Link to={`/post/${post.post_id}`}>{post.title}</Link>
                      </td>
                      <td>{post.status}</td>
                    </tr>
                  ))
              ) : (
                <tr>
                  <td colSpan='3'>No posts available</td>
                </tr>
              )}
            </tbody>
          </table>
        </div>
  
        <div>
          <h1 className='mt-4'>Reported Posts</h1>
          <table className='table table-bordered'>
            <thead className='thead-dark'>
              <tr>
                <th scope='col'>Index</th>
                <th scope='col'>Title</th>
                <th scope='col'>Reason</th>
                <th scope='col'>Actions</th>
              </tr>
            </thead>
            <tbody>
              {Array.isArray(reportedposts) && reportedposts.length > 0 ? (
                reportedposts.map((reportedPost, index) => (
                  <tr key={index}>
                    <td>{index + 1}</td>
                    {/* Find the corresponding post in the posts array */}
                    {posts.find((post) => post.post_id === reportedPost.post_id) ? (
                      <>
                        <td>
                          <Link to={`/post/${reportedPost.post_id}`}>
                            {posts.find((post) => post.post_id === reportedPost.post_id).title}
                          </Link>
                        </td>
                        <td>{reportedPost.reason}</td>
                        <td>
                          <button
                            className='btn btn-danger mr-2'
                            onClick={() => handleDelete(reportedPost.post_id)}
                          >
                            Delete
                          </button>
                          {posts.find((post) => post.post_id === reportedPost.post_id).status !==
                          'unpublished' ? (
                            <button
                              className='btn btn-warning'
                              onClick={() => handleUnpublish(reportedPost.post_id)}
                            >
                              Unpublish
                            </button>
                          ) : null}
                        </td>
                      </>
                    ) : (
                      <>
                        <td colSpan='2'>Post not found</td>
                        <td></td>
                      </>
                    )}
                  </tr>
                ))
              ) : (
                <tr>
                  <td colSpan='4'>No reported posts available</td>
                </tr>
              )}
            </tbody>
          </table>
        </div>
      </div>
    );
  }
  
  export default ModeratorHomepage;
  