import React, { useEffect, useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';

function UsersCompo() {
  const [users, setUsers] = useState([]);
  const nav=useNavigate();
  useEffect(() => {
    // Fetch data from API endpoints for Users
    const fetchData = async () => {
      try {
        const usersResponse = await fetch('https://mern.up.railway.app/getusers');
        const usersData = await usersResponse.json();
        setUsers(usersData);
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    };
    fetchData();
  }, []);

  const handleStatusUpdate = async (userId, newStatus) => {
    try {
      // Make API call to update user status
      await fetch(`https://mern.up.railway.app/updatestatus/${userId}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ status: newStatus }),
      });

      // Update the status in the local state
      setUsers((prevUsers) =>
        prevUsers.map((user) =>
          user.user_id === userId ? { ...user, status: newStatus } : user
        )
      );
    } catch (error) {
      console.error('Error updating status:', error);
    }
  };
  return (
    <section className='container my-5'>
      <h2 style={{ textAlign: 'center' }}>Users</h2>
      <div className='d-flex justify-content-end'><button  className='btn btn-success' onClick={()=>nav('/Admin/CreateUser')}>Create User</button></div>
      {users.length === 0 ? (
        <p>No users available</p>
      ) : (
        <table className="table">
          <thead>
            <tr>
              <th scope="col">User ID</th>
              <th scope="col">Name</th>
              <th scope="col">Email</th>
              <th scope="col">Password</th>
              <th scope="col">Status</th>
              <th scope="col">Action</th>
            </tr>
          </thead>
          <tbody>
            {users.map((user) => (
              <tr key={user.user_id}>
                <th scope="row">{user.user_id}</th>
                <td><Link to={`/Admin/UpdateUser/${user.user_id}`}>{user.name}</Link></td>
                <td>{user.email}</td>
                <td>{user.pass}</td>
                <td>{user.status}</td>
                <td>
                  {user.status === 'moderator' && (
                    <button className='btn btn-warning' onClick={() => handleStatusUpdate(user.user_id, 'user')}>Make User</button>
                  )}
                  {user.status === 'user' && (
                    <button className='btn btn-info' onClick={() => handleStatusUpdate(user.user_id, 'moderator')}>Make Mod</button>
                  )}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      )}
      </section>
  );
}

export default UsersCompo;
