import React, { useState, useEffect } from 'react';
import { useLocation } from 'react-router-dom';

function EditProfile() {
  const location = useLocation();
  const id =
    location.pathname.split('/HomePage/')[1] ||
    location.pathname.split('/Aboutus/')[1] ||
    location.pathname.split('/ContactUs/')[1] ||
    location.pathname.split('/EditProfile/')[1];
  const [user, setUser] = useState({
    user_id: '',
    name: '',
    email: '',
    pass: '',
  });

  const [newUserData, setNewUserData] = useState({
    name: '',
    email: '',
    pass: '',
  });

  const [verifiedEmail, setVerifiedEmail] = useState(false);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // Fetch user data using the appropriate API endpoint
    const fetchUserData = async () => {
      try {
        const response = await fetch(`https://mern.up.railway.app/users/${parseInt(id)}`);
        const userData = await response.json();
        setUser(userData);
        setNewUserData({
          name: userData.name,
          email: userData.email,
          pass: userData.pass,
        });
        setLoading(false);
      } catch (error) {
        console.error('Error fetching user data:', error);
        setLoading(false);
      }
    };

    // Assuming userId is available from the component props or state
    const userId = parseInt(id); // Replace with the actual user id
    fetchUserData();
  }, [id,verifiedEmail]);

  const handleChange = (e) => {
    setNewUserData({ ...newUserData, [e.target.name]: e.target.value });
  };

  const handleVerifyEmail = () => {
    // Additional logic to verify email
    const isEmailVerified = newUserData.email === user.email;
    setVerifiedEmail(isEmailVerified);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      // Make API call to update user information
      const response = await fetch(`https://mern.up.railway.app/updateuser/${id}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(newUserData),
      });

      if (response.ok) {
        // Handle successful update (e.g., show a success message)
        alert('User information updated successfully');
      } else {
        alert('Failed to update user information:', response.status);
      }
    } catch (error) {
      console.error('Error updating user information:', error);
    }
  };

  return (
    <div className='container p-5'>
      {loading ? (
        <p>Loading...</p>
      ) : (
        <form onSubmit={handleSubmit}>
          <div className="form-group">
            <label htmlFor="name">Name</label>
            <input
              type="text"
              className="form-control"
              id="name"
              name="name"
              value={newUserData.name}
              onChange={handleChange}
            />
          </div>
          <div className="form-group">
            <label htmlFor="email">Email</label>
            {verifiedEmail ? (
              <input
                type="text"
                className="form-control"
                id="email"
                name="email"
                value={newUserData.email}
                onChange={handleChange}
              />
            ) : (
              <>
                <input
                  type="text"
                  className="form-control"
                  id="email"
                  name="email"
                  onChange={handleChange}
                  disabled={verifiedEmail}
                />
                <button
                  type="button"
                  className={`btn btn-${verifiedEmail ? 'success' : 'primary'}`}
                  onClick={handleVerifyEmail}
                  disabled={verifiedEmail}
                >
                  {verifiedEmail ? 'Verified' : 'Verify'}
                </button>
                {verifiedEmail && (
                  <small className="form-text text-success">Email verified!</small>
                )}
              </>
            )}
          </div>
          <div className="form-group">
            <label htmlFor="pass">Password</label>
            <input
              type="text"
              className="form-control"
              id="pass"
              name="pass"
              value={newUserData.pass}
              onChange={handleChange}
            />
          </div>
          <button type="submit" className="btn btn-primary">
            Save Changes
          </button>
        </form>
      )}
    </div>
  );
}

export default EditProfile;
