import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";

export const Login = (props) => {
    const [email, setEmail] = useState('');
    const [pass, setPass] = useState('');
    const [incorrectAttempts, setIncorrectAttempts] = useState(0);
    const [disabledUntil, setDisabledUntil] = useState(null);
    const nav = useNavigate();
    const handleSubmit = async (e) => {
        e.preventDefault();
      
        if (disabledUntil && new Date() < new Date(disabledUntil)) {
          // User is currently disabled, don't attempt login
          alert('Login disabled. Please try again later.');
          return;
        }
      
        const url = `https://mern.up.railway.app/users?email=${email}&pass=${pass}`;
        const response = await fetch(url, {
          method: 'GET',
          headers: {
            'Content-Type': 'application/json',
          },
        });
      
        if (response.ok) {
          // Successful login, reset incorrectAttempts
          setIncorrectAttempts(0);
      
          // Extract user ID from the response
          const data = await response.json();
          const userId = data.userId;
      
          // Pass user ID to the next page
          nav(`/HomePage/${userId}`);
        } else {
          // Increment incorrectAttempts
          setIncorrectAttempts(incorrectAttempts + 1);
      
          if (incorrectAttempts + 1 >= 5) {
            // If 5 or more incorrect attempts, disable login for 5 minutes
            const disabledUntilTime = new Date();
            disabledUntilTime.setMinutes(disabledUntilTime.getMinutes() + 5);
            setDisabledUntil(disabledUntilTime);
          }
      
          // Display an alert for login failure
          alert('Login failed. Please check your credentials.');
        }
      }
      
      

    useEffect(() => {
        // Reset disabledUntil after 5 minutes
        if (disabledUntil && new Date() >= new Date(disabledUntil)) {
            setDisabledUntil(null);
        }
    }, [disabledUntil]);

    return (
        <div className="auth-form-container">
            <h2> User Login</h2>
            <form className="login-form" onSubmit={handleSubmit}>
                <label htmlFor="email">Email</label>
                <input value={email} onChange={(e) => setEmail(e.target.value)} type="email" placeholder="youremail@gmail.com" id="email" name="email" />
                <label htmlFor="password">Password</label>
                <input value={pass} onChange={(e) => setPass(e.target.value)} type="password" placeholder="********" id="password" name="password" />
                <button type="submit">Log In</button>
            </form>
            <button className="link-btn" onClick={() => props.onFormSwitch('register')}>Don't have an account? Register here.</button>
        </div>
    )
}
