import React, { useState, useEffect } from 'react';

function Comments() {
  const [comments, setComments] = useState([]);
  const [posts, setPosts] = useState([]);

  useEffect(() => {
    // Fetch comments
    fetch('https://mern.up.railway.app/comments')
      .then((response) => response.json())
      .then((data) => setComments(data))
      .catch((error) => console.error('Error fetching comments:', error));

    // Fetch posts
    fetch('https://mern.up.railway.app/')
      .then((response) => response.json())
      .then((data) => setPosts(data))
      .catch((error) => console.error('Error fetching posts:', error));
  }, []);

  const handleDeleteComment = async (commentId) => {
    try {
      // Make an API call to delete comment
      await fetch(`https://mern.up.railway.app/deletecomment/${commentId}`, {
        method: 'DELETE',
        headers: {
          'Content-Type': 'application/json',
        },
      });

      // Update the local state after successful deletion
      setComments((prevComments) => prevComments.filter((comment) => comment.comment_id !== commentId));
    } catch (error) {
      console.error('Error deleting comment:', error);
    }
  };

  const handleUpdateBody = async (commentId, newBody) => {
    try {
      // Make an API call to update the body of the comment
      await fetch(`https://mern.up.railway.app/comments/${commentId}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ body: newBody }),
      });

      // Update the local state after successful update
      setComments((prevComments) =>
        prevComments.map((comment) =>
          comment.comment_id === commentId ? { ...comment, body: newBody } : comment
        )
      );
    } catch (error) {
      console.error('Error updating comment body:', error);
    }
  };

  return (
    <div className="container mt-5">
      <h2>Comments</h2>
      <table className="table">
        <thead>
          <tr>
            <th>Comment ID</th>
            <th>Date</th>
            <th>Time</th>
            <th>Body</th>
            <th>Post Title</th>
            <th>Action</th>
          </tr>
        </thead>
        <tbody>
          {comments.map((comment) => (
            <tr key={comment.comment_id}>
              <td>{comment.comment_id}</td>
              <td>{comment.date}</td>
              <td>{comment.time}</td>
              <td>{comment.body}</td>
              <td>{posts.find((post) => post.post_id === comment.post_id)?.title}</td>
              <td>
                <button
                  className="btn btn-danger me-2"
                  onClick={() => handleDeleteComment(comment.comment_id)}
                >
                  Delete
                </button>
                <button
                  className="btn btn-primary"
                  onClick={() => {
                    const newBody = prompt('Enter the new body:');
                    if (newBody !== null) {
                      handleUpdateBody(comment.comment_id, newBody);
                    }
                  }}
                >
                  Update Body
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}

export default Comments;
