import React, { useState, useEffect } from 'react';
import ReactQuill from 'react-quill';
import DOMPurify from 'dompurify';
import 'react-quill/dist/quill.snow.css';
import { useNavigate, useParams } from 'react-router-dom';

function EditPost() {
  const { id } = useParams();
  const [post, setPost] = useState({
    title: '',
    body: '',
    date: '',
    time: '',
    status: '',
  });
  const nav=useNavigate();
  const fetchPostDetails = async () => {
    try {
      const response = await fetch(`https://mern.up.railway.app/post/${id}`);
      if (!response.ok) {
        throw new Error(`HTTP error! Status: ${response.status}`);
      }
      const postDetails = await response.json();
      setPost(postDetails);
    } catch (error) {
      console.error('Error fetching post details:', error);
    }
  };

  useEffect(() => {
    fetchPostDetails();
  }, [id]);

  const handleUpdatePost = async () => {
    try {
      const response = await fetch(`https://mern.up.railway.app/posts/${id}/update`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(post),
      });

      if (!response.ok) {
        throw new Error(`HTTP error! Status: ${response.status}`);
      }
      nav('/Admin/Posts')
      // Optionally, you can redirect the user to the updated post or another page
    } catch (error) {
      console.error('Error updating post:', error);
    }
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setPost((prevPost) => ({ ...prevPost, [name]: value }));
  };

  const handleBodyChange = (value) => {
    setPost((prevPost) => ({ ...prevPost, body: value }));
  };

  const modules = {
    toolbar: [
      [{ 'header': '1' }, { 'header': '2' }],
      ['bold', 'italic', 'underline', 'strike', 'blockquote'],
      [{ 'list': 'ordered' }, { 'list': 'bullet' }],
      ['link', 'image', 'video'],
      ['clean'],
    ],
  };

  const formats = [
    'header', 'font', 'size',
    'bold', 'italic', 'underline', 'strike', 'blockquote',
    'list', 'bullet', 'link', 'image', 'video',
  ];

  return (
    <div className="container mt-5">
      <h2>Edit Post</h2>
      <form>
        <div className="mb-3">
          <label className="form-label">Title:</label>
          <input
            type="text"
            className="form-control"
            name="title"
            value={post.title}
            onChange={handleInputChange}
          />
        </div>

        <div className="mb-3">
          <label className="form-label">Body:</label>
          <ReactQuill
            theme="snow"
            value={post.body}
            onChange={handleBodyChange}
            modules={modules}
            formats={formats}
            placeholder="Write something amazing..."
          />
        </div>

        {/* Include other fields like date, time, and status as needed */}

        <button type="button" className="btn btn-primary" onClick={handleUpdatePost}>
          Update Post
        </button>
      </form>
    </div>
  );
}

export default EditPost;
